import {comingSoon,newMovies,top250} from '../../pages/apis/index.js'
import {mapState} from 'vuex'
export default {
	data() {
		return {
			moveList:[]  // 电影数据列表
		}
	},
	onReady() {
		// this.$store.dispatch('getCity')
		this.pageInit()
	},
	computed:{
		...mapState({
			city:state => state.city,
			noPlayingList: state => state.noPlayingList
		})
	},
	methods: {
		// 一起调用一个接口，目的是只触发一个loading
		pageInit(){
			uni.showLoading({
			    title: '加载中'
			})
			Promise.all([
				this._comingSoon(),
				this._newMovies(),
				this._top250()
			]).then(res =>{
				uni.hideLoading()
				let rel = [];
				res.map((item) =>{
					rel.push(item[1].data)
				})
				this.moveList = rel
			})
		},
		toDetails(id){
			uni.navigateTo({
				url: '../details/details?id='+ id,
			});
		},
		// 页面跳转事件
		toClass(index){
			uni.navigateTo({
				url: '../class/class?index='+ index,
			});
		},
		// 调用接口
		_comingSoon(){
			// return 一个promise对象出来
			return comingSoon({
				start:'0',
				count:'8',
				city: this.city
			})
		},
		// 调用接口
		_newMovies(){
			return newMovies({
				start:'0',
				count:'8',
				city: this.city
			})
		},
		// 调用接口
		_top250(){
			return top250({
				start:'0',
				count:'8',
				city: this.city
			})
		},
	}
}
